@import 'src/styles/_variables';
@import 'src/styles/_breakpoints';

.separator {
    background-color: $black;
    height: 7px;

    margin: 100px 100px 50px;

    @media (max-width: $tablet) {
        margin: 50px
    }
}

.contactDescription {
    text-align: center;
    font-family: MavenPro-Semibold, sans-serif;
    font-size: 20px;
    color: $black;
    
    @media (max-width: $mobile) {
        font-size: 16px;
    }
}

.contactLinks {
    display: flex;
    justify-content: space-around;

    margin: 80px 150px;

    @media (max-width: $tablet) {
        margin: 40px
    }
}